/* stylelint-disable color-function-notation */
/* stylelint-disable alpha-value-notation */
:root {
  --content-width: 1720px;
  --content-fluid-width: 1920px;
  --container-offset: 100px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --container-fluid-width: calc(var(--content-fluid-width) + (var(--container-offset) * 2));
  --main-scrollbar-width: 6px;
  --main-scrollbar-color: var(--secondary);
  --main-scrollbar-thumb-color: var(--primary);
}

:root {
  --base-font: "Inter", sans-serif;
}

:root {
  --white: #ffffff;
  --black: #000000;
  --primary: #8b00ff;
  --secondary: #cf95d8;
  --warning: #fff500;
  --danger: ;
  --info: #674185;
  --gray-10: #fafafa;
  --gray-20: #F0F0F0;
  --gray-30: #ebebeb;
  --gray-60: #a8a8a8;
  --base-text-color: var(--black);
  --main-bg-color: var(--white);
  --placeholder-color: var(--gray-8);
}

