*,
*::before,
*::after {
  box-sizing: inherit;
  border-radius: 0;
}

html {
  box-sizing: border-box;
}

html,
body {
  width: 100%;
  min-height: 100vh;
  min-height: fill-available;
  min-height: -webkit-fill-available;
  -webkit-text-size-adjust: none;
}

body {
  position: relative;
  display: flex;
  flex-direction: column;
  font-family: var(--base-font, sans-serif);
  font-weight: 400;
  line-height: 1.2;
  color: var(--base-text-color);
  background-color: var(--main-bg-color);
}
body.body-locked {
  overflow: hidden;
}

footer {
  margin-top: auto;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
  color: var(--base-text-color);
}

/* flex*/
.flex-wrap, .main-nav__list, .main-header__action, .main-header__container {
  display: flex;
  flex-wrap: wrap;
}

.flex-centered-content, .main-nav__list-item a, .main-btn {
  display: flex;
  align-items: center;
  justify-content: center;
}

.flex-column, .main-header__contacts {
  display: flex;
  flex-direction: column;
}

/*text*/
.text-truncate {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

/*scroll*/
.main-scroll::-webkit-scrollbar, ::-webkit-scrollbar {
  background-color: var(--main-scrollbar-color);
  width: var(--main-scrollbar-width);
}
.main-scroll::-webkit-scrollbar-thumb, ::-webkit-scrollbar-thumb {
  background-color: var(--main-scrollbar-thumb-color);
}

/*resets*/
.list-reset, .main-nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.fieldset-reset {
  margin: 0;
  padding: 0;
  border: 0;
}

.btn-reset, .main-btn {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #ffffff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

/*transitions*/
.hover-opacity {
  transition: opacity var(--main-transition);
}
.hover-opacity:hover, .hover-opacity:focus {
  opacity: 0.7;
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.container-fluid {
  width: 100%;
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-fluid-width);
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

/* Компоненты */
.main-btn {
  width: fit-content;
  min-height: 50px;
  padding: 14px 24px;
}
.main-btn img {
  margin-right: 10px;
}
.main-btn--primary {
  border-radius: 100px;
  color: var(--white);
  background: radial-gradient(183.6% 73.98% at 1.29% -21.15%, #FFF500 6.05%, rgba(139, 0, 255, 0) 100%), radial-gradient(57.04% 43.55% at 69.19% 116.35%, #06F 0%, rgba(0, 224, 255, 0) 100%), linear-gradient(276deg, #8B00FF 58.9%, #EB00FF 105.39%);
  box-shadow: 0px 30px 60px 0px rgba(178, 0, 255, 0.5);
}
/* Секции */
.main-header {
  padding: 24px 0;
}
.main-header__container {
  align-items: center;
}
.main-header__action {
  margin-left: auto;
}
.main-header__contacts {
  align-items: flex-end;
  padding: 0 10px;
  margin-right: 20px;
}
.main-header__contacts a {
  font-size: 24px;
}
.main-header__contacts a:last-child {
  font-size: 15px;
  color: var(--info);
}

.main-nav {
  padding: 0 30px;
  flex-grow: 1;
}
.main-nav__list {
  align-items: center;
  width: 100%;
}
.main-nav__list-item {
  flex-grow: 1;
}
.main-nav__list-item a {
  padding: 15px;
  color: var(--base-text-color);
}

/* Модальные окна */